.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

#container {
  height: 100%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.shadow_box {
  border: 1px, solid, rgb(49, 108, 135);
  border-radius: 15px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  width: 300px;
  height: 420px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.shadow_box form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 250px;
}

.btn_container {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.form-group {
  width: 100%;
  margin-bottom: 10px;
}

.form-group h3 {
  text-align: center;
}

.form-control {
  width: 97%;
  height: 28px;
  margin-top: 5px;
}